import Body from "./Components/Helpers/Body";
import AppContext from "../Context/AppContext";
import { useContext, useEffect, useState } from "react";

const Settings = () => {
    //const { accounts, } = useContext(AppContext);

    const { currency, setCurrency } = useContext(AppContext);

    const [currencies, setCurrencies] = useState();
    const [data, setData] = useState(false);
    //select currenct

    async function getCurrencies() {
        const res = await fetch('https://api.freecurrencyapi.com/v1/latest?apikey=fca_live_PHA3zQsZt5P7OiZrfRvW7LDDVC7hc3e0ghVIvHAV')
        const currencies = await res.json();

        return await currencies;
    }

    async function processCurrencies(obj) {
        let val = Object.values(await obj);
        let arr = Object.keys(await val[0]).map(function (key, index) {

            return { [`${key}`]: val[0][`${key}`] }
        });

        setCurrencies(arr)
    }


    function selectCurrencyHandler(e) {
        setCurrency(e.target.value)
        console.log(e.target.value)
    }

    useEffect(() => {

        let currency = getCurrencies();

        processCurrencies(currency)
        console.log(currency);

    }, [])

    return (

        <div className="content">
            <h1>Settings</h1>
            <div className="content_display">
                <Body className="Settings">

                    <select onChange={selectCurrencyHandler}>
                        {
                            currencies ? currencies.map((d, i) => {
                                return <option key={i} value={Object.keys(d)}> {Object.keys(d)} </option>
                            }) : ""
                        }

                    </select>


                </Body>
            </div>
        </div>

    );

}
export default Settings
